import React from 'react'
import './Home.css'
function Home() {
    return (
        <div>
            <section id="header" className="d-flex align-items-center">
            <div className="container-fluid nav_bg">
                <div className="row">
                    <div className="col-10 mx-auto">
                      <div className="row">
                        <div className="col-md-6 pt-5 pt-lg-0 order-1 order-lg-1 d-flex justify-content-center flex-column">
                            <h1>
                             The Most Powerful <strong className="brand-name"> Checking Account</strong>
                             </h1>
                             <h2 className="my-3">
                             Our checking account gives you higher returns than a savings account with no hidden fees.
                             </h2>
                             <div id="inp__t"  className="input-group mb-3">
                             
                                <input id="ip"  type="text" class="form-control" placeholder="Enter Email Address" aria-label="Enter Email Address" aria-describedby="basic-addon2" />
                                    
                                 <div class="input-group-append"> 
                                     <button id="bt__n" class="btn btn-outline-secondary" type="button">  Join Now</button>
                                 </div>
                                 
                             </div>
                             <h2 id="header__2" className="my-3">
                                <img id="im__g1" src="https://juno-public.s3-us-west-1.amazonaws.com/Juno/createaccount-homeaddress-flag%403x.png" /> 260 spots left for Priority Access.
                             </h2>
                        </div>

                        <div className="col-lg-6 order-2 order-lg-2 header-img">
                            <img id="im__g" src="https://juno-public.s3-us-west-1.amazonaws.com/Juno/juno-mobile-app%403x.png" className="img-fluid animated" alt="home img" />
                        </div>
                        </div>

                    </div>
                </div>
            </div>

        </section>
        </div>
    )
}

export default Home
