import React from 'react'
import './Navbar.css'


function Header() {
    return (
        <div className="navBar">
            
            <nav className="navbar navbar-expand-lg navbar-light bg-light">
            <img 
            className="navBar__icon"
            src="https://juno-public.s3-us-west-1.amazonaws.com/on-juno%402x.png"
                
            alt=""
            />
  
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav mr-auto">
      <li className="nav-item active" >
        <a  className="nav-link"  style={{marginLeft: 80}}  href="#" >Home <span class="sr-only">(current)</span></a>
      </li>
      
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" style={{marginLeft: 80}} href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Company
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <a className="dropdown-item" href="#">About</a>
          <a className="dropdown-item" href="#">Newsroom</a>
          <a className="dropdown-item" href="#">Careers</a>
          <a className="dropdown-item" href="#">Partners</a>
        </div>
      </li>
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" style={{marginLeft: 80}} href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Learn
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <a className="dropdown-item" href="#">Blog</a>
          <a className="dropdown-item" href="#">Guides</a>
          <a className="dropdown-item" href="#">Help Center</a>
        </div>
      </li>
      <li className="nav-item dropdown">
        <a className="nav-link dropdown-toggle" style={{marginLeft: 80}} href="#" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
          Legal
        </a>
        <div className="dropdown-menu" aria-labelledby="navbarDropdown">
          <a className="dropdown-item" href="#">Privacy Policy</a>
          <a className="dropdown-item" href="#">Terms of Use</a>
          <a className="dropdown-item" href="#">Trademarks</a>
        </div>
      </li>
      
    </ul>
    <div>
    <button type="button" id="btn1" class="btn btn-light" style={{marginLeft:80} }  >Login</button></div>
        
    <button type="button" id="btn2" class="btn btn-primary" style={{marginLeft:80}}>Signup</button>
    </div>
  
</nav>
        </div>
    )
}

export default Header
